.slide {
    display: flex;
    position: relative;
    width: 100%;
    height: 255px;
    margin: 2% 0;
    overflow: hidden;
  
    @media screen and (min-width: $breakpoint-desktop) {
      height: 415px;
    }
  
  }
  .slider {
    @include WH-full;
    position: absolute;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    border-radius: 10px;
    opacity: 0;
    transition: opacity ease-in-out 0.6s; //small effect of gradual appearance of the image in the slide
  
    @media screen and (min-width: $breakpoint-desktop) {
      border-radius: 15px;
    }
    & img {
      @include WH-full;
      object-fit: cover;
      position: absolute;
      left: 0%;
      right: 0%;
      top: 0%;
      bottom: 0%;
      mix-blend-mode: normal;
      border-radius: 10px;
  
      @media screen and (min-width: $breakpoint-desktop) {
        border-radius: 15px;
      }
    }
    &__number {
      display: none;
  
      @media screen and (min-width: $breakpoint-desktop) {
        display: block;
        position: absolute;
        @include font(18px, 500);
        color: $tertiary-color;
        left: 48%;
        top: 86%;
    
      }
    }
  }
  .active {
    opacity: 1;
  }
  
  .right-Arrow {
    @include rightArrow-style;
  }
  
  .left-Arrow {
    @include leftArrow-style;
  }
  
  