.banner {
    border-radius: 10px;
    height: 111px;
    margin-bottom: 35px;
    position: relative;
    @media screen and (min-width: $breakpoint-desktop) {
      border-radius: 25px;
      height: 223px;
    }
  
    &__img {
      border-radius: 25px;
      height: 100%;
      object-fit: cover;
      width: 100%;
    }
    &__sombre {
      background-color: $background-color;
      width: 100%;
      height: 100%;
      border-radius: 25px;
      left: 0;
      opacity: 0.3;
      position: absolute;
      top: 0;
    }
    &__txt {
      position: absolute;
      top: 28.04%;
      left: 5.6%;
      right: 32.53%;
      bottom: 80.05%;
      font-style: Medium;
      font-size: 24px;
      font-weight: 500;
      line-height: 100%;
      color: $tertiary-color;
      width: 217px;
      height: 48px;
      @media screen and (min-width: $breakpoint-desktop) {
        top: 0;
        left: 0;
        right: 0;
        @include flex-center;
        @include WH-full;
        @include font(48px, 500);
      }
    }
  }
  