.footer {
    // @extend %flex-column;
    @extend %flex-center;
    // gap: 50px;
    width: 100%;
    height: 209px;
    margin-top: 5%;
    background-color: $footer-color;
    flex-direction: column;
    &__logo {
      margin-bottom: 2%;
      width: 122px;
      height: 40px;
    }
    &__text-copyright {
      @include font(12px, 500);
      @extend %flex-end;
      color: $tertiary-color;
      width: 100%;
  
      @media screen and (min-width: $breakpoint-desktop) {
        @include font(24px, 500);
      }
    }
  }
  